import { useState, useEffect, useContext } from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, Card, Form, Button } from 'react-bootstrap';

import { nativeSignIn, signUp } from '../utils/fetchData';
import { SwitchContainerFunc, SetUserName } from '../containers/App';

import '../sign.css';

// https://bootsnipp.com/snippets/z1Bpy

const Sign = () => {

  const [isSignInShown, setIsSignInShown] = useState(true);
  const [signInButtonState, setSignInButtonState] = useState('disabled');
  const [signUpButtonState, setSignUpButtonState] = useState('active');

  function handleSignButtonClick(event) {
    switch (event) {
      case 'signIn':
        setIsSignInShown(true);
        setSignInButtonState('disabled');
        setSignUpButtonState('active');
        break;
      case 'signUp':
        setIsSignInShown(false);
        setSignInButtonState('active');
        setSignUpButtonState('disabled');
        break;
      default:
        break;
    }
  }

  return (
    <Container >
      <Row>
        <Col md={{ span: 6, offset: 3 }}>
          <Card className="panel-login">
            <Card.Title>
              <Row >
                <Col xs={6}>
                  <Button className={signInButtonState} id="login-form-link" onClick={() => handleSignButtonClick('signIn')}>Login</Button>
                </Col>
                <Col xs={6}>
                <Button className={signUpButtonState} id="login-form-link" onClick={() => handleSignButtonClick('signUp')}>Register</Button>
                </Col>
              </Row>
            </Card.Title>
            <Card.Body>
              <Row>
                <Col lg={12}>
                  {isSignInShown ? <SignIn /> : <SignUp />}
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

const SignIn = () => {
  const switchToContainer = useContext(SwitchContainerFunc);
  const setUserName = useContext(SetUserName);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  return (
    <Form id="login-form" action="" method="post" role="form" style={{display: 'block'}}>
      <Form.Group>
        <input type="text" name="email" id="email" tabIndex="1" className="form-control" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)}></input>
      </Form.Group>
      <Form.Group>
        <input type="password" name="password" id="password" tabIndex="2" className="form-control" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)}></input>
      </Form.Group>
      <Form.Group>
        <input type="checkbox" tabIndex="3" className="" name="remember" id="remember"></input>
        <label htmlFor="remember"> Remember Me</label>
      </Form.Group>
      <Form.Group>
        <Row>
          <Col sm={{ span: 6, offset: 3 }}>
            <Button name="login-submit" id="login-submit" tabIndex="4" className="form-control btn btn-login" onClick={() => nativeSignIn(email, password, switchToContainer, setUserName)}>LOG IN</Button>
          </Col>
        </Row>
      </Form.Group>
      <Form.Group>
        <Row>
          <Col lg={{ span: 6, offset: 3 }}>
            <div className="text-center">
              <a href="https://phpoll.com/recover" tabIndex="5" className="forgot-password">Forgot Password?</a>
            </div>
          </Col>
        </Row>
      </Form.Group>
    </Form>
  );
};

const SignUp = () => {

  const switchToContainer = useContext(SwitchContainerFunc);
  const setUserName = useContext(SetUserName);

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  return (
    <Form id="register-form" action="" method="post" role="form" style={{display: 'block'}}>
      <Form.Group>
        <input type="text" name="username" id="username" tabIndex="1" className="form-control" placeholder="Username" value={name} onChange={(e) => setName(e.target.value)}></input>
      </Form.Group>
      <Form.Group>
        <input type="email" name="email" id="email" tabIndex="1" className="form-control" placeholder="Email Address" value={email} onChange={(e) => setEmail(e.target.value)}></input>
      </Form.Group>
      <Form.Group>
        <input type="password" name="password" id="password" tabIndex="2" className="form-control" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)}></input>
      </Form.Group>
      <Form.Group>
        <Row>
          <Col sm={{ span: 6, offset: 3 }}>
            <Button name="register-submit" id="register-submit" tabIndex="4" className="form-control btn btn-register" onClick={() => signUp(name, email, password, switchToContainer, setUserName)}>REGISTER NOW</Button>
          </Col>
        </Row>
      </Form.Group>
    </Form>
  );
};

export default Sign;