/* eslint-disable no-unused-vars*/

import 'bootstrap/dist/css/bootstrap.min.css';

import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

const Header = ( { switchToContainer, userName } ) => {
  return (
    <>
    <Navbar bg="dark" variant="dark">
      <Container>
        <Navbar.Brand href="#home">DocuBloom</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link href="#editor" onClick={() => switchToContainer('editor')}>New Doc</Nav.Link>
          <Nav.Link href="#profile" onClick={() => switchToContainer('profile')}>Profile</Nav.Link>
          <Nav.Link href="#sign" onClick={() => switchToContainer('sign')}>Sign in/up</Nav.Link>
        </Nav>
        <Nav>
          <Nav.Link disabled style={{'marginLeft': 'auto'}}>{userName}</Nav.Link>
        </Nav>
      </Container>
    </Navbar>
    </>
  );
};

export default Header;