/* eslint-disable no-unused-vars*/
import { useState, useEffect } from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';

import { UpdaterInput } from '../utils/Input';
import { fetchUserDocById, updateUserDocById } from '../utils/fetchData';

const Updater = ( { docId, docValue, setDoc, switchToContainer } ) => {
  const token = localStorage.getItem('token');

  const [initDoc, setInitDoc] = useState('test');

  useEffect(() => {
    fetchUserDocById(token, docId, setDoc).then(data => setInitDoc(data));
  }, []);

  function updateDoc() {
    updateUserDocById(token, docId, JSON.parse(docValue));
    switchToContainer('profile');
  }

  return (
    <>
      <Container>
        <Button href="#viewer" variant="info" onClick={() => switchToContainer('viewer')}>View</Button>
        <Button variant="info" onClick={updateDoc}>Save</Button>
        <UpdaterInput
          exampleValue = {initDoc}
          setDoc = { setDoc }
        />
      </Container>
    </>
  );
};

export default Updater;