/* eslint-disable no-unused-vars*/

import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';

import { EditorInput } from '../utils/Input';
import { addUserDoc } from '../utils/fetchData';

const Editor = ( { initDoc, docValue, setDoc, switchToContainer } ) => {
  const token = localStorage.getItem('token');

  function addNewDoc() {
    addUserDoc(token, JSON.parse(docValue));
    switchToContainer('profile');
  }

  return (
    <>
      <Container>
        <Button href="#viewer" variant="info" onClick={() => switchToContainer('viewer')}>View</Button>
        <Button variant="info" onClick={() => addNewDoc()}>Save</Button>
        <EditorInput
          exampleValue = { initDoc }
          setDoc = { setDoc }
        />
      </Container>
    </>
  );
};

export default Editor;